@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300&display=swap');
 
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Mulish', sans-serif;
  max-width: 100%;
}
/* ***************** navbar *************** */
.navbar {
  background-color: #fff;
}
.navbar-brand {
  font-size: 1.5rem !important;
  color: #565387 !important;
}

.navbar a {
  font-size: 1rem;
  text-transform: capitalize;
}

.menu_active {
  font-weight: bold;
  border-bottom: 1px solid #565387;
}

.navbar a:hover {
  color: #3498db !important;
}
/* ***************** ****** *************** */

/* ***************** home *************** */
#header {
  margin-top: 70px;
  height: 80vh;
}
#header h1 {
  margin: 0 0 10px 0;
  /* font-size: 48px; */
  font-weight: 700;
  color: #000;
}

#header .brand-name {
  color: #3498db;
}
.btn-get-started {
  text-decoration: none;
  letter-spacing: 1px;
  display: inline-block;
  border: 2px solid #3498db;
  color: #3498db;
  padding: 0.4rem 1rem;
  border-radius: 50px;
  transition: 0.5s;
}
.btn-get-started:hover {
  background: #3498db;
  color: #fff;
}
.header-img {
  text-align: right;
}
/* *********************************** */

/* *********** img-animation ***************** */
img.animated {
  -webkit-animation: mover 1s infinite  alternate;
  animation: mover 1s infinite  alternate;
}
img.animated {
  -webkit-animation: mover 1s infinite  alternate;
  animation: mover 1s infinite  alternate;
}
@-webkit-keyframes mover {
  0% { transform: translateY(10); }
  100% { transform: translateY(-10px); }
}
@keyframes mover {
  0% { transform: translateY(10); }
  100% { transform: translateY(-10px); }
}
/* ************************************************ */

.contact_div {
  width: 100%;
  height: 75vh;
}

@media (max-width: 991px) {
  #header {
    height: 80vh;
    text-align: center;
  }
  #header .header-img {
    text-align: center;
  }
  #header .header-img img {
    width: 60%;
  }
  .menu_active {
    border-bottom: none;
  }
}
@media (max-width: 768px) {
  #header {
    margin-top: 20px;
  }
  #header h1 {
    font-size: 28px;
  }
  #header h2 {
    font-size: 18px;
    margin-bottom: 30px;
  }
  #header .header-img img {
    width: 70%;
  }
} 